import React, { Component } from "react";

class Closed extends Component {
	render() {
		return (
			<div id="timer">
				<p>Thank you for participating in this season of gratitude 🥳</p>
				<h1>2684 💌</h1>
				<span>gratitudes were delivered offline <br/></span>
				<span>and</span>
				<h1>748 💌</h1>
				<span>e-postcards were delivered online</span>
				<p>
					And remember,
					<br />
					You don't need a postcard to express the heavenly feeling of
					gratitude,
					<br /> so just go and make someone's day...
				</p>
				<img src="/images/girl-writing.jpg" alt="Illustration" />
			</div>
		);
	}
}

export default Closed;
